import React, { Component } from 'react';
import './InterviewTable.css';
import data from './data.json';
import {Link, Redirect, withRouter} from 'react-router-dom';
import {link} from "../../App";
import Swal from 'sweetalert2';
import 'moment-timezone';
var moment = require('moment');



export default class InterviewTable extends Component {

    constructor(props){
        super(props);
        this.state={
            ar:[],
            dd: [],
            user:[]
        };
    }

    myFunction= (idd,ttime) => {
        let ids = {"id": idd};
        let time = ttime;
        document.getElementById(idd).style.backgroundColor = "gray";
        Swal.fire({text: `Время вашего интервью: ${time}`,
            showCancelButton:true,
            cancelButtonText:'Нет',
            cancelButtonColor:'red',
            confirmButtonText:'Да',
            confirmButtonColor:'#32B381',}).then((result) => {
            if (!result.value) {
                return;
            }
            Swal.fire({
                html: '<p style="font-family: Raleway;\n' +
                    'font-weight: 600;\n' +
                    'font-size: 24px;\n' +
                    'text-align: center;\n' +
                    '\n' +
                    'color: #000000;">Вы успешно прошли регистрацию на собеседование. </p>' +
                    '</br><p style="font-size: 24px;">' +
                    '<button id="reg", style="font-family: Raleway;font-style: normal;#reg.active box-shadow: 2px 2px 2px 2px black;\n' +
                    '} \n' +
                    '}' +
                    'font-weight: 600; line-height: normal;' +
                    'font-size: 100%;\n' +
                    'text-align: center;\n' +
                    'color: #FFFFFF; background: #32B482;\n' +
                    'border-radius: 20px; width: 41%; height: 80%; border: none; min-height: 80px;' +
                    ' max-height: 80px;" ><a href = "https://neobis.kg" style="text-decoration: none; ' +
                    'color: #FFFFFF; font-weight: 600; font-size: 24px; @media screen and (max-width: 768px){' +
                    'a{font-size:18px;}}@media screen and (max-width: 550px){' +
                    'a{font-size: 12px;}}">Вернуться на главную страницу</a></button>',
                width: 900,
                height: 500,
                showConfirmButton: false
            }).then(
                fetch(`${link}/interview/time_slots/`, {
                    method: 'POST',
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        'Authorization': `Token ${this.props.location.state.responseAnswer.token}`
                    },
                    body: JSON.stringify({
                        "id": idd
                    }),
                })
            )
        });
    };
    timeslot;

    async componentDidMount(){
        if(this.props.location.state === undefined){this.props.history.goBack();}
        const userData = this.props.location.state.responseAnswer;
        const get = await fetch(`${link}/interview/time_slots/`, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${this.props.location.state.responseAnswer.token}`
            },
        });
        const getuser = await fetch(`${link}/interview/user_info/`, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${this.props.location.state.responseAnswer.token}`
            },
        });
        const userjsons = await getuser.json();
        if (getuser.ok){
            console.log(userjsons);
            var timeslot = userjsons.timeslot;
            Swal.fire({title: `Привет, ${userjsons.name}!</br>
            Университет: ${userjsons.university}
            Департамент: ${userjsons.department}
            Время твоего интервью: </br> ${userjsons.time}
            Хочешь поменять время интервью?`,
                showCancelButton:true,
                cancelButtonText:'<a href = "https://neobis.kg"; style="color: #FFFFFF; font-weight: 200; font-size: 15px;">Вернуться на главную страницу</a>',
                cancelButtonColor:'gray',
                confirmButtonText:'Да',
                confirmButtonColor:'#32B381',})}
        const json = await get.json();
        if (get.ok){
            console.log(json);
            const allDates = Object.keys(json);
            let buttons=[];
            for(var i = 0; i < allDates.length; i++){
                var date = allDates[i];
                buttons.push(json[allDates[i]]);
            }
            await this.setState({ ar: buttons, dd:allDates, user: timeslot });
            console.log(this.state.timeslot);
        }else{
            this.props.history.goBack();
        }

    };


    render(){
        const names = this.state.dd;
        let table = document.getElementById('table');
        if(this.state.ar !== []) {
            this.state.ar.map((one) => {
                let first = document.createElement('th');
                one.map((two)=>{
                    let child = document.createElement('button');
                    child.className="buttons";
                    child.id = two.id;
                    child.time = two.time;
                    child.is_free = two.is_free;
                    child.onclick = () => {this.myFunction(two.id, two.time)};
                    first.appendChild(child);
                    if (two.id == this.state.user ){
                        child.style.backgroundColor  = "#F5C5C5";
                    }

                    if (two.is_free==false){
                        child.style.backgroundColor = "#C4C4C4";
                        child.disabled = true;}
                })
                table.appendChild(first);
            });
        }


        return(

            <table>
                <thead>
                <th id='none'></th>
                <th id ='dates'>
                    {names.map(date =>(<td >{date}</td>))}
                </th>
                </thead>
                <th id="time">
                    {data.map(row =>(<tr id = 'datta'>{row.time}</tr>))}
                </th>
                <div id="table"></div>
            </table>
        )

    }
}






