import React, { Component } from 'react';
import {withRouter} from "react-router-dom";
import './Login.css';
import {link} from "../../App";

const Swal = require('sweetalert2');

class LogIn extends Component{

    handleSubmit = (event) =>
    {
        event.preventDefault();
        const email = document.getElementById('LogEmail').value;
        const phone = document.getElementById('LogPhone').value;
        const loginData = {
            username: email,
            password: phone,
        };
        console.log(JSON.stringify(loginData));
        fetch(`${link}/application/login/`, {
                method: 'POST', // or 'PUT'
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(loginData), // data can be `string` or {object}!
            }).then((e) => {
                if (e.ok) {
                    e.json().then(( responseAnswer => {
                        this.props.history.push({
                            pathname: '/interview',
                            state: {responseAnswer: responseAnswer}
                        });
                    }));
                }
                else {
                    Swal.fire({
                        type: 'error',
                        title: 'Oops...',
                        text: 'Неверный логин или пароль',
                        confirmButtonColor: '#32B482',
                    });
                }
            });
    };

    render(){
        return(
            <div className="authContainer">
                <form onSubmit={this.handleSubmit}>
                    <input id="LogEmail"
                           className="LogEmail"
                           type="LogEmail"
                           name = "Email"
                           required
                           placeholder="E-mail"
                    />
                    <input id="LogPhone"
                           className="LogPhone"
                           type="tel"
                           name = "Phone"
                           required
                           pattern="\+[0-9]{12}"
                           placeholder="+996123456789"
                    />
                        <button className="logIn" >Войти</button>
                </form>
            </div>
        )
    }

}

export default withRouter(LogIn);
