        const department = document.getElementById("f_select_department").value;
        const experience = document.getElementById("f_experience").value;
        const hobby = document.getElementById("f_hobby").value;
        const reason = document.getElementById("f_reason").value;

        // console.log(this.state.grad_year);
        // console.log(this.state.universities);

