import React, {Component} from 'react';
import MainPage from "./containers/MainPage/MainPage";
import {Route, Switch} from "react-router-dom";
import ApplicationPage from "./containers/ApplicationPage/ApplicationPage";
import RegistrationPage from "./containers/RegistrationPage/RegistrationPage";
import LogIn from "./components/logInComponent/Login";
import InterviewTable from "./components/InterviewTable/InterviewTable";

export const link = 'https://project-hr.herokuapp.com';

class App extends Component {
    render() {
        return (
            <Switch>
                <Route path='/' exact component={MainPage}/>
                <Route path='/application_old' exact component={ApplicationPage}/>
                <Route path='/application' exact component={RegistrationPage}/>
                <Route path='/log_in' exact component={LogIn}/>
                <Route path='/interview' exact component={InterviewTable}/>
            </Switch>
        );
    }
}

export default App;